import { useEffect, useState } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useMasterchef, useMasterEnigma } from './useContract'
import useRefresh from './useRefresh'
import useBlock from './useBlock'

const useIsMember = () => {
  const [val, setVal] = useState(false)
  const { account }: { account: string } = useWallet()
  const { fastRefresh } = useRefresh()
  const masterChefContract = useMasterchef()
  useEffect(() => {
    if (account && !val) masterChefContract.methods.isMember().call({ from: account }).then(setVal)
  }, [account, val, masterChefContract, fastRefresh])
  return [val]
}

export const useIsWinner = () => {
  const [val, setVal] = useState(undefined)
  const { account }: { account: string } = useWallet()
  const block = useBlock()

  const contract = useMasterEnigma()
  useEffect(() => {
    if (account) contract.methods.getUserLastResult(account).call().then(setVal)
  }, [account, contract, block])
  return val
}

export default useIsMember
